/*
font-family: 'IBM Plex Mono', monospace;
font-family: 'Inter', sans-serif;
*/

:root {
  --mainColor: #155EFF;
  --blackColor: black;
}

body {
  background: var(--blackColor);
  color: white;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
}

button {
  font-family: 'Inter', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul>li {
  list-style-type: none;
}

.container {
  width: 1200px;
  margin: 0 auto;
}

a {
  color: white;
  text-decoration: none;
}

/* MENU */
.mobileMenu {
  display: none;
}

.menu {
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menuNumber {
  display: flex;
  align-items: center;
  color: white;
  text-decoration: none;
  font-size: 16px;
}

.menuNumber img {
  margin-right: 10px;
}

.menuLinks ul {
  display: flex;
}

.menuLinks li {
  margin-right: 50px;
}

.menuLink {
  transition: 0.3s;
  cursor: pointer;
}

.menuLink:hover {
  color: var(--mainColor);
}

.menuLinkLine {
  transition: 0.3s;
  border: none;
  height: 4px;
  width: auto;
  background: transparent;
  top: 30px;
  position: relative;
}

.menuLink:hover .menuLinkLine {
  background: var(--mainColor);
}

.menuWrapper {
  box-shadow: inset 0px -1px 0px rgba(255, 255, 255, 0.2);
}

.menuLang {
  display: flex;
  align-items: center;
}

.menuLang img {
  margin-right: 10px;
}

.menuLinkActive {
  color: var(--mainColor);
}

.menuLinkActive hr {
  background: var(--mainColor);
}

/* HOMEPAGE */

.homepageBody {
  padding-top: 150px;
}


.siteTitle {
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 700;

}

.homepageHeaderInfo {
  width: 70%;
}

.homepageTitle {
  font-size: 56px;
  line-height: 64px;
  letter-spacing: 1px;
}

.homepageTitle span {
  color: var(--mainColor);
}

.homepageHeaderDesc {
  margin-top: 50px;
  margin-bottom: 50px;
  font-weight: 500;
  font-size: 14px;
  line-height: 26px;
  color: #FFFFFF;
  opacity: 0.9;
  width: 60%;
}

.homepageHeaderDownload {
  padding: 20px 40px;
  color: white;
  border: none;
  background: transparent;
  transition: 0.3s;
  font-weight: 500;
  cursor: pointer;
  font-size: 14px;
  align-items: center;
  display: flex;
}

.homepageHeaderDownload:hover {
  color: var(--mainColor);
}

.homepageHeaderButtons {
  display: flex;
}

.homepageHeaderDownload img {
  margin-right: 10px;
}

.homepageHeaderDown {
  display: inline-flex;
  align-items: center;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 12px;
  margin-top: 130px;
  cursor: pointer;
  transition: 0.3s;
}
.homepageDownText {
  transition: 0.3s;
}
.homepageHeaderDown:hover .homepageDownText{
  color: var(--mainColor);
}

.homepageHeaderDown svg {
  margin-right: 10px;
}

.homepageHeaderDownIcon {
  transition: 0.3s;
}

.homepageHeaderDown:hover .homepageHeaderDownIcon {
  fill: var(--mainColor);
}

.homepageHeaderImage {
  width: 30%;
  height: 600px;
  background: url('/public/images/headerImageBG.svg');
  background-repeat: no-repeat;
  position: relative;
}

.homepageHeaderImage img {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 80%;
  margin: auto;
}

.homepageHeader {
  display: flex;
  justify-content: space-between;
}

.verticalText {
  writing-mode: vertical-lr;
  margin-right: 100px;
  color: var(--mainColor);
  text-transform: uppercase;
  transform: rotate(180deg);
  letter-spacing: 4px;
  text-align: right;
}

.homepageAdv {
  position: relative;
  margin-top: 100px;
  display: flex;
}

.homepageAdvTitle {
  font-size: 40px;
  line-height: 68px;
  width: 60%;
}

.homepageAdvTitle span {
  border-bottom: 4px solid var(--mainColor);
}

.homepageAdvCard {
  width: 33%;
  margin-bottom: 50px;
}

.homepageAdvCardTitle {
  font-size: 18px;
  margin-top: 40px;
  margin-bottom: 20px;
}

.homepageAdvCards {
  margin-top: 60px;
  display: flex;
  flex-wrap: wrap;
}

.homepageAdvDesc {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  opacity: 0.8;
  width: 300px;
}

.homepageProduction {
  margin-top: 50px;
}

.homepageProductionTitle {
  font-weight: 900;
  font-size: 40px;
  line-height: 48px;
  margin-bottom: 40px;
}

.homepageProduction {
  display: flex;
}

.homepageProductionTitle span {
  border-bottom: 4px solid var(--mainColor);
}

.homepageProductDesc {
  font-size: 14px;
  width: 400px;
  line-height: 24px;
}

.homepageProductDescList {
  margin-top: 20px;
  margin-bottom: 40px;
}

.homepageProductDescList p {
  margin-top: 15px;
  margin-bottom: 15px;
}


.homepageProduct {
  background: url('../public/images/homepageProductBackground.svg');
  width: 300px;
  height: 390px;
  border: 1px solid #ffffff5e;
  transition: 0.3s;
  cursor: pointer;
}

.homepageProduct:hover {
  background: url('../public/images/homepageProductBackgroundActive.svg');
}

.homepageProductionList {
  margin-top: 50px;
}

.homepageProduct p {
  font-size: 20px;
  line-height: 24px;
  color: #FFFFFF;
  font-family: 'IBM Plex Mono', monospace;
  margin-bottom: 40px;
}

.homepageProduct {
  position: relative;
}

.homepageProductBody {
  position: absolute;
  left: 40px;
  bottom: 40px;
}

.homepageProductionList {
  display: flex;
  justify-content: space-between;
}

.homepageProductionBlock {
  width: 100%;
  max-width: 980px;
}

.homepagePartners {
  margin: 0 auto;
  padding-top: 125px;
  padding-bottom: 125px;
  display: flex;
  margin-top: 125px;
  width: 1200px;

}

.homepagePartners .siteTitle {
  width: 60%;
}

.homepagePartnersDesc {
  margin-top: 50px;
  margin-bottom: 50px;
  font-family: 'Inter';
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #FFFFFF;
  opacity: 0.9;
  width: 40%;
}

.homepagePartnersContainer {

  background: #141414;
}

.homepagePartnerTitle {
  font-weight: 900;
  font-size: 24px;
  line-height: 32px;
  text-transform: uppercase;
  color: white;
  transition: 0.3s;
  width: 100% !important;
}

.homepagePartner:hover .homepagePartnerTitle {
  color: var(--mainColor);
}

.homepagePartnerDesc {
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  color: #FFFFFF;
  opacity: 0.6;
}

.homepagePartnersList {
  display: flex;
}

.homepagePartner {
  cursor: pointer;
  margin-right: 100px;
}

.homepageAbout {
  display: flex;
  padding-top: 125px;
  padding-bottom: 125px;
}

.homepageAboutTitle {
  font-weight: 900;
  font-size: 40px;
  line-height: 48px;
  color: #FFFFFF;
  width: 45%;
}

.homepageAboutTitle span {
  border-bottom: 4px solid var(--mainColor);
}

.homepageAboutText {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
}

.homepageAboutText p {
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  margin-bottom: 20px;
}

.homepageAboutText div {
  width: 45%;
}

.homepageAbout span {
  color: var(--mainColor);
}

.homepageImage img {
  width: 100%;
  height: 800px;
  object-fit: cover;
}

.homepageImageMask {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 5;
  left: 0;
  top: 0;

  background: url('/public/images/maskImage.svg');
  opacity: 0.4;
}

.homepageImage {
  position: relative;

}

.homepageBlog {
  display: flex;
  width: 100%;
  margin-top: 125px;
  margin-bottom: 125px;
}

.homepageBlogTitle span {
  border-bottom: 4px solid var(--mainColor);
}

.homepageBlogTitle {
  font-weight: 900;
  font-size: 40px;
  line-height: 48px;
  color: #FFFFFF;
  width: 30%;
}

.homepageBlogList {
  display: flex;
  justify-content: space-between;
}

.homepageBlogItem {
  width: 300px;
  height: 490px;
  border-left: 1px solid #eff1f52f;
  border-right: 1px solid #eff1f52f;
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 80px;
  padding-bottom: 80px;
  transition: 0.3s;
  cursor: pointer;
  background: var(--blackColor);
}

.homepageBlogItem:hover {
  background: url('/public/images/blogBG.svg');
  background-size: cover;
  border-top: 1px solid #eff1f52f;
  border-bottom: 1px solid #eff1f52f;
}

.homepageBlogItemDate {
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  color: #FFFFFF;
  opacity: 0.6;
}

.homepageBlogItemTitle {
  margin-top: 50px;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  margin-bottom: 50px;
  transition: 0.3s;

}

.homepageBlogItemDesc {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #FFFFFF;
  opacity: 0.6;
  transition: 0.3s;
  margin-bottom: 50px;
}

.homepageBlogList {
  margin-top: 90px;
  width: 90%;
}

.homeBlogContent {
  width: 100%;
}

.homepageBlogItem:hover .homepageBlogItemTitle {
  color: var(--mainColor);
}

/* CONTACT BLOCK */

.contactBlock {
  padding-top: 125px;
  padding-bottom: 125px;
  display: flex;
}

.contactTitle {
  font-weight: 900;
  font-size: 40px;
  line-height: 48px;
  color: #FFFFFF;
}

.contactTitle span {
  border-bottom: 4px solid var(--mainColor);
}

.contactBlockText {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  opacity: 0.9;
  width: 250px;
}

.contactBlockText {
  margin-bottom: 20px;
}

.contactTitle {
  margin-bottom: 40px;
}

.contactBlockTextTitle {
  margin-bottom: 10px;
  opacity: 0.6;
}

.contactBlockContentText a {
  color: var(--mainColor);
}

.contactBLockContent {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.contactBlockContentForm {
  display: inline-grid;
  width: 480px;
}

.contactBlockContentForm input,
textarea {
  height: 50px;
  padding-left: 20px;
  border: none;
  background: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 0.367);
  outline: navajowhite;
  font-family: 'Inter';
  font-weight: 400;
  color: white;
  transition: 0.3s;
  resize: none;
}

.contactBlockContentForm input:focus,
textarea:focus {
  border-bottom: 1px solid var(--mainColor);

}

.contactBlockContentForm input::placeholder,
textarea {
  font-family: 'Inter';
  font-weight: 600;
}

.contactBlogSend {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  cursor: pointer;
}

.contactBlogSendText {
  transition: 0.3s;
}

.contactBlogSend:hover .contactBlogSendText {
  color: #0140CC;
}

.contactBlogSend p {
  margin-right: 10px;
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
}

.contactBlockContentForm textarea {
  padding-top: 10px;
  padding-right: 20px;
}

.contactBlockSendButton {
  width: 48px;
  height: 48px;
  background: linear-gradient(205.69deg, #0150FF 16.2%, #7A93CB 31.19%, #0140CC 66.24%);
  border-radius: 100%;
  position: relative;
}


.contactBlockSendButton svg {
  position: absolute;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

footer {
  padding-top: 40px;
  padding-bottom: 40px;
  border-top: 1px solid #242933;
}

.footerBody {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footerBody {
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 24px;
  text-align: right;
  color: #FFFFFF;
  opacity: 0.8;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--mainColor);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* ADAPTIVE */

@media (max-width: 1240px) {
  .container {
    width: 90%;
    margin: 0 auto;
  }

  .homepageTitle {
    font-size: 46px;
    line-height: 54px;
  }

  .homepageHeaderInfo {
    width: 60%;
  }

  .homepageHeaderImage {
    width: 25%;
    height: 450px;
  }
}

@media (max-width: 1140px) {
  .homepageTitle {
    font-size: 42px;
  }

  .homepageAdvDesc {
    width: 90%;
  }

  .homepageProduct {
    width: 30%;
    height: 390px;
  }

  .homepageProductBody {
    left: 30px;
    bottom: 30px;
  }

  .verticalText {
    margin-right: 50px;
  }

  .homepagePartners {
    width: 90%;
  }

  .homepagePartners .siteTitle {
    width: 70%;
  }

  .homepagePartner {
    margin-right: 70px;
  }

  .homepageAboutTitle {
    width: 55%;
  }

  .homepageBlogTitle {
    width: 50%;
  }

  .homepageBlogItem {
    width: 30%;
    height: 400px;
    padding-top: 20px;
  }

  .homepageBlogItemTitle {
    margin-top: 40px;
    font-size: 14px;
    margin-bottom: 40px;
    transition: 0.3s;
  }
}

@media (max-width: 1024px) {
  .homepageAboutText p {
    font-size: 14px;
  }

  .homepagePartners .siteTitle {
    width: 78%;
  }

  .homepageTitle {
    font-size: 35px;
    line-height: 48px;
  }

  .homepageImage img {
    width: 100%;
    height: 500px;
    object-fit: cover;
  }

  .homepageBlogList {
    flex-wrap: wrap;
  }

  .homepageBlogItem {
    width: 45%;
    height: 400px;
    padding-top: 20px;
    margin-bottom: 50px;
    border: 1px solid #eff1f52f;
  }

  .contactBlockContentForm {
    display: inline-grid;
    width: 50%;
  }

  .contactBlockContentText {
    width: 50%;
  }

  .contactBlockText {
    width: 70%;
  }

  .menuLinks li {
    margin-right: 20px;
  }
}

@media (max-width: 768px) {

  .menuNumber,
  .menuLinks,
  .menuLang {
    display: none;
  }

  .hamburger {
    width: 50px;
    height: 50px;
  }

  .hamburger-inner,
  .hamburger-inner::before,
  .hamburger-inner::after {
    background-color: white !important;
  }

  .hamburger {
    outline: none;
  }

  .mobileMenu {
    height: 100vh;
    background: #141414;
    color: white;
    width: 100vw;
    position: fixed;
    right: -100%;
    top: 0;
    z-index: 999;
    transition: 0.6s;
    transition-timing-function: cubic-bezier(1, 0.01, 0.31, 0.98);
    display: block;
  }

  .menuActive {
    right: 0;
  }

  .mobileMenuHeader {
    padding-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .mobileMenuHeader {
    width: 90%;
    margin: 0 auto;
    border-bottom: 1px solid rgba(255, 255, 255, 0.404);
    padding-bottom: 20px;
  }

  .mobileMenuLinks {
    width: 90%;
    margin: 0 auto;
    margin-top: 50px;
  }

  .menuLink {
    font-size: 32px;
    font-weight: 600;

  }

  .mobileMenuTitle {
    font-size: 28px;
  }

  .menuLinkLine {
    display: none;
  }

  .mobileMenuLinks ul li {
    margin-top: 40px;
  }

  .menuLinkActive {
    color: #0140CC;
  }

  .homepageHeader {
    display: block;
  }

  .homepageHeaderDown {
    display: none;
  }

  .homepageHeaderInfo {
    width: 90%;
  }

  .homepageTitle {
    font-size: 45px;
    line-height: 58px;
  }

  .homepageHeaderDesc {
    font-size: 18px;
    width: 90%;
  }

  .homepageHeaderImage {
    margin-top: 50px;
  }

  .homepageHeaderImage {
    width: 100%;
    height: 450px;
    background-size: cover;
  }

  .homepageHeaderImage img {
    width: 50%;
  }

  .homepageAdvTitle {
    font-size: 40px;
    width: 100%;
  }

  .homepageAdvCard {
    width: 40%;
    margin-bottom: 50px;
  }

  .homepageAdvCards {
    justify-content: space-between;
  }

  .homepageProductionList {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .homepageProduct {
    width: 45%;
    height: 390px;
    margin-bottom: 50px;
  }

  .homepagePartnersBlock {
    width: 100%;
  }

  .homepagePartnersList {
    flex-wrap: wrap;
  }

  .homepagePartners .siteTitle {
    width: 100%;
  }

  .homepagePartnersDesc {
    width: 80%;
  }

  .homepagePartner {
    margin-bottom: 20px;
  }

  .homepageAboutTitle {
    width: 95%;
  }

  .homepageAboutText {
    display: block;
  }

  .homepageAboutText div {
    width: 100%;
  }

  .homepageBlogTitle {
    width: 60%;
  }

  .homepageBlogItem {
    width: 48%;
  }

  .contactBLockContent {
    display: block;
  }

  .contactBlockContentText {
    width: 100%;
  }

  .contactBlockContentForm {
    margin-top: 40px;
    display: inline-grid;
    width: 100%;
  }

  .contactBlockContentForm input,
  textarea {
    margin-top: 20px;
  }

  .contactBlogSend {
    margin-top: 20px;
  }

  .homepageBlog {
    margin-bottom: 0px;
  }

  .menuHamburger {
    display: block !important;
  }
}

@media (max-width: 600px) {
  .homepageTitle {
    font-size: 35px;
    line-height: 48px;
  }

  .homepageBody {
    padding-top: 100px;
  }

  .homepageAdvTitle {
    font-size: 32px;
    width: 100%;
  }

  .homepageAdvTitle {
    line-height: 58px;
  }

  .homepageAdvCard {
    width: 45%;
  }

  .homepageProduct {
    height: 300px;
  }

  .verticalText {
    margin-right: 30px;
  }

  .siteTitle {
    font-size: 32px;
  }

  .verticalText {
    margin-right: 30px;
    font-size: 12px;
  }

  .homepagePartnersList {
    display: block;
    justify-content: space-between;
    margin: 0 auto;
    text-align: center;
  }

  .homepageBlogItem {
    width: 90%;
    margin: 0 auto;
    margin-bottom: 40px;
  }

  .footerText {
    width: 50%;
  }
}

@media (max-width: 490px) {
  .homepageHeaderInfo {
    width: 100%;
  }

  .siteTitle {
    font-size: 28px;
    line-height: 35px;
  }

  .homepageHeaderDesc {
    font-size: 16px;
    width: 90%;
    margin-top: 25px;
  }

  .homepageHeaderImage {
    height: 250px;
  }

  .homepageProductionList {
    display: block;
  }

  .homepageProduct {
    margin: 0 left;
    width: 80%;
    height: 400px;
    margin-bottom: 50px;
    background-size: cover;
  }

  .homepageProduct:hover {
    background-size: cover;
  }

  .siteTitle {
    font-size: 24px;
    line-height: 35px;
  }

  .homepageAdvCard {
    width: 90%;
  }

  .homepageProductDesc {
    font-size: 14px;
    width: 90%;
    line-height: 24px;
  }
}

@media (max-width: 430px) {
  .filledButton {
    padding: 15px 20px;
  }

  .homepageHeaderDownload {
    padding: 15px 20px;
  }
}

@media (max-width: 375px) {
  .homepageBody {
    padding-top: 50px;
  }

  .siteTitle {
    font-size: 22px;
  }

  .homepageHeaderDesc {
    font-size: 14px;
  }

  .siteTitle {
    font-size: 20px;
  }

  .homepageProduct {
    height: 350px;
  }

  .homepagePartnersDesc {
    width: 100%;
  }

  .homepageBlogItem {
    width: 100%;
  }
}

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
  display: block !important;
}

.menuHamburger {
  display: none;
}

/* PRODUCTS */

.productsBody {}

.productsBodyHeader {
  padding-top: 50px;
  padding-bottom: 50px;
}

.productsBodyNavigation {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
}

.activeLink {
  color: var(--mainColor);
}

.productsBodyNavigation span {
  margin-right: 15px;
}

.productsBodyTitle {
  margin-top: 100px;
  font-weight: 900;
  font-size: 72px;
  line-height: 80px;
  letter-spacing: 1px;
  text-transform: capitalize;
