.filledButton {
    padding: 20px 40px;
    color: white;
    border: none;
    background: var(--mainColor);
    transition: 0.3s;
    font-weight: 500;
    border: 2px solid var(--mainColor);
    cursor: pointer;
    font-size: 14px;

}
.filledButton:hover {
    background: transparent;
}