.circleButton {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    background: var(--blackColor);
    border: 1px solid var(--mainColor);
    transition: 0.3s;
    text-align: center;
    position: relative;
}

.circleButton:hover {
    background: white;
    color: var(--blackColor);
    border: 1px solid white;
}

.circleButton svg {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    left: 0;
    right: 0;
}
.circleButton:hover .circleButtonIcon {
    fill: var(--blackColor)
}