.arrowLink {
    color: var(--mainColor);
    transition: 0.3s;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 500;
}
.arrowLink svg {
    margin-left: 10px;
}
.arrowLinkIcon {
    transition: 0.3s;
}
.arrowLink:hover {
    color: white;
}
.arrowLink:hover .arrowLinkIcon{
    fill: white;
}