.textCircleButton {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    border: 1px solid var(--mainColor);
    position: relative;
    background: var(--blackColor);
    transition: 0.3s;
}
.homepageBlogItem:hover, .fileList:hover .textCircleButton{
    background: white;
    border: 1px solid white;
    transform: translateX(90px);
}
.homepageBlogItem:hover, .fileList:hover .textCircleButtonText{
   opacity: 1;
}
.homepageBlogItem:hover, .fileList:hover .textCircleButtonIcon {
    fill: var(--blackColor);
}

.textCircleButton svg {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
}

.textCircleButtonLink {
    display: flex;
    align-items: center;
}

.textCircleButtonText {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    color: var(--mainColor);
    position: absolute;
    opacity: 0;
    transition: 0.3s;
    text-transform: uppercase;
}

